body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: #4b464b;

}
.wrap{
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}
header{
  z-index: 3;
  background-color: rgba(51, 51, 51, 0.5);
  max-width: 100%;
  width: 100%;
  margin: 0 auto;
  position: absolute;

}
.logo{
  font-size: 15px;
  font-family: sans-serif;
  text-transform: uppercase;
  margin: 0 0 0 2%;
  width: 72%;
  float:left;
}
.logo a{
  text-decoration: none;
  color: #f6f6f6;
}
.note-container{
    padding-top: 80px;
}
.flexwrap{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
    padding-top: 80px;
}
.app{
  position: relative;
  background-image:url("./images/note.png");
  height: 100vh;
  background-size: cover ;
  background-repeat: no-repeat;
}
.addUser{
  z-index: 2;
  width:100%;
  height: 100vh;
  background-image: url("./images/pencils.png");
  background-size: cover ;
  background-repeat: no-repeat;
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.none{
  display: none;
    opacity: 0;
}
.active{
    display: -ms-flexbox;
    display: flex;
}
.home{
    float: right;
    border: 0;
    border-radius: 15px;
    background-color: transparent;
    color: white;
    font-size: 40px;
    outline: none;
    margin: 10px 15px 0 0;
}
.home:hover{
    cursor: pointer;
    background-color: whitesmoke;
    color: black
}
.login{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-align: center;
        align-items: center;
    background-color: rgba(31, 31, 37, 0.69);
    border-radius: 20px;
    height: 50vh;
}
.loginForm{
    margin: 0 17vmin 0 17vmin;
}
.loginForm p{
  margin: 20px 0 10px 10px;
  color: whitesmoke;
}
.loginForm input[type="text"],
.loginForm input[type="password"]{
  width: 90%;
  height: 30px;
  margin: 0 5% 0 5%;
  background-color: rgba(0,0,0,0);
  border: 1px solid whitesmoke;
  border-radius: 7px;
  color: whitesmoke;
}
.loginForm .center{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.loginForm button{
  margin-top: 15px
}
.note{
  font-family: "Courier New",Courier,monospace;
  max-width: 400px;
  padding: 21px 15px 15px 15px;
  margin: 8px;
  border-radius: 15px;
  border: 1px solid lightgrey;
  position: relative;
}
.plus{
    z-index: 1;
    width: 230px;
    height: 240px;
    position: absolute;
    padding: 21px 28px 25px 15px;
    border-radius: 15px;
    top: 0;
    left: 0;
}
.plusButton{
    border: 0;
    background-color: transparent;
    font-size: 160px;
    color: #b4760e;
    margin: 30px 0 10px 60px;
    outline: none;
}
.note .date{
  position: absolute;
  bottom: 10px;
  right: 7px;
  font-size: 15px;
}
.note .notetext{
  margin-bottom: 25px;
  font-style: italic;
}
.note h3{
  display: block;
  border-bottom: 2px dotted #4b464b;
  margin-top: 5px;
}
.noteform p{
  margin-top: 3px;
  margin-bottom: 3px;
}
.noteform button{
  position: absolute;
  bottom: 10px;
  left: 50px;
}
.noteform p input[type=text]{
  margin-bottom: 5px;
  border: 0;
  border-bottom: 1px solid #4b464b;
}
.noteform ul{
  float:right;
  list-style-type: none;
  padding:0;
}
.noteform textarea{
  float:left;
  height: 20vmin;
  margin-bottom: 30px;
  border-radius: 7px
}


input[type=text], textarea,input[type="password"]{
  outline: none;
  border: 1px solid #bfbfbf;
}
textarea:focus {
  -webkit-box-shadow: 0 0 5px rgba(81, 203, 238, 1);
          box-shadow: 0 0 5px rgba(81, 203, 238, 1);
  border: 1px solid rgba(81, 203, 238, 1);
}

.yellow{
  background-color: #ffe2a8;
}
.red{
  background-color: #ffc1b8;

}
.blue{
  background-color: #aae2ff;
}
.green{
  background-color: #8cff91;
}
.noteform textarea{
  resize: none;
}
.note p{
  min-width: 85px;
  word-break: break-all;
}
.note .date{
  text-align: right;
  margin: 0;
}
.button {
  background: #a3999e;
  border-radius: 7px;
  padding: 5px 20px;
  color: #faf0fa;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  outline: none;
}
.editbuttons{
  position: absolute;
  top: 5px;
  right: 5px;
}
.editbuttons .button{
  padding: 1.5px 4px;
  font-size: 12px;
}

/* Hover state animation applied here */
.button:hover {
  -webkit-animation: hover 1200ms linear 2 alternate;
  animation: hover 1200ms linear 2 alternate;
}

/* Active state animation applied here */
.button:active {
  -webkit-animation: active 1200ms ease 1 alternate;
  animation: active 1200ms ease 1 alternate;
}

/* Active state animation keyframes below */

@-webkit-keyframes active {
  0% {-webkit-transform: scale(1,1);transform: scale(1,1);}
  90% {-webkit-transform: scale(.9,.88);transform: scale(.9,.88);}
  100% {-webkit-transform: scale(.92,.9);transform: scale(.92,.9);}
}

@keyframes active {
0% {-webkit-transform: scale(1,1);transform: scale(1,1);}
90% {-webkit-transform: scale(.9,.88);transform: scale(.9,.88);}
100% {-webkit-transform: scale(.92,.9);transform: scale(.92,.9);}
}

