.label1,
.label2,
.label3,
.label4{
  display: inline;
  -ms-flex-align: center;
      align-items: center;
  padding: 5px 0;
  margin-top: 5px;
  color: #4b464b;
  cursor: pointer;
  -webkit-transition: all 0.25s linear;
  -o-transition: all 0.25s linear;
  transition: all 0.25s linear;
}
.label1:hover,
.label2:hover,
.label3:hover,
.label4:hover{
  color: #b4b4b4;
}
.label1::before,
.label2::before,
.label3::before,
.label4::before{
  display: inline-block;
  content: "";
  height: 6px;
  width: 6px;
  margin-right: 4px;
  margin-top: 2px;
  border: 3px solid #a3999e;
  border-radius: 50%;
  -webkit-transition: all 0.25s linear;
  -o-transition: all 0.25s linear;
  transition: all 0.25s linear;
}
.radio {
  position: absolute;
  opacity: 0;
  visibility: hidden;
}
.radio:checked + .label1,
.radio:checked + .label2,
.radio:checked + .label3,
.radio:checked + .label4{
  font-style: italic;
}

.radio:checked + .label1::before{
  background: green;
}
.radio:checked + .label2::before{
  background: blue;
}
.radio:checked + .label3::before{
  background: red;
}
.radio:checked + .label4::before{
  background: yellow;
}





